.todolist {
  border: 2px solid burlywood;
  border-radius: 5px;
  padding: 20px;
  background-color: darkseagreen;
  box-shadow: 0 0 20px 0 black;
}
.todolist-empty {
  border: 2px solid burlywood;
  border-radius: 5px;
  padding: 20px;
background-color: coral;
  box-shadow: 0 0 20px 0 black;
}
.task {
  font-weight: bold;
}
.task-not-isDone{
  font-weight: normal;
text-decoration: line-through;
}
.filter-btn{
  border: none;
  border-radius: 3px;

}
.filter-btn-active{
  background-color: dodgerblue;
  color: white;
}

.filter-btn-wrapper {
  display: flex;
  justify-content: space-between;

}
ul {
  list-style: none;
  padding: 0 ;
}
li {
  display: flex;
  justify-content: space-between;
  padding: 2px 0;
}
h3{
  text-align: center;
}
.input-error {
  border: 2px solid red;
  outline: none;
}
.add-form{
  background-color: darkseagreen;
  min-height: fit-content;
  border: 2px solid burlywood;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 0 20px 0 black;
}
.form-wrapper {
  display: flex;
  align-items: center;
}
